'use client';

import { useRef, useEffect, useState } from 'react';
import useMessages from '@/hooks/useMessages';
import useUserProfile from '@/hooks/useUserProfile';
import { Avatar } from '@mui/material';

export default function ChatWindow({
  conversationId,
}: {
  conversationId?: string;
}) {
  const { user, isLoading: userLoading } = useUserProfile();
  const { messages, send, loading, error } = useMessages(conversationId);
  const [draft, setDraft] = useState('');
  const bottomRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    bottomRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const submit = async () => {
    if (!draft.trim() || !user || !conversationId) return;
    await send(draft.trim(), user.id, user.name ?? 'You');
    setDraft('');
  };

  if (!conversationId)
    return (
      <p className="text-center py-10 text-gray-500">
        Select a conversation to start chatting.
      </p>
    );

  if (userLoading || loading)
    return <p className="text-center py-10">Loading…</p>;

  return (
    <div className="flex flex-col h-full">
      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {error && <p className="text-red-500">{error}</p>}

        {messages.map(m => {
          const mine = m.senderId === user.id;
          const letter = mine
            ? (user.name?.[0] ?? 'U').toUpperCase()
            : (m.senderName?.[0] ?? 'U').toUpperCase();

          return (
            <div
              key={m.id}
              className={`flex items-center ${mine ? 'justify-end' : 'justify-start'}`}
            >
              {!mine && (
                <Avatar
                  sx={{ bgcolor: '#ed9426', width: 32, height: 32, mr: 2 }}
                >
                  {letter}
                </Avatar>
              )}

              <div
                className={`max-w-xs p-3 rounded-lg ${
                  mine
                    ? 'bg-blue-100 text-gray-900'
                    : 'bg-green-100 text-gray-800'
                }`}
              >
                {m.body}
              </div>

              {mine && (
                <Avatar
                  sx={{ bgcolor: '#ed9426', width: 32, height: 32, ml: 2 }}
                >
                  {letter}
                </Avatar>
              )}
            </div>
          );
        })}

        <div ref={bottomRef} />
      </div>

      <div className="p-3 pb-20 border-t flex gap-2">
        <input
          className="flex-1 border rounded px-3 py-2"
          value={draft}
          onChange={e => setDraft(e.target.value)}
          onKeyDown={e => e.key === 'Enter' && submit()}
          placeholder="Type a message…"
        />
        <button
          className="bg-orange-500 text-white rounded px-4 disabled:opacity-50"
          onClick={submit}
          disabled={!draft.trim()}
        >
          Send
        </button>
      </div>
    </div>
  );
}
